import {
  Authentication,
  Device,
  Fleet,
  IEvent,
  ModuleData,
} from "@formant/data-sdk";
import "@formant/ui-sdk-realtime-player";
import React from "react";

import "./App.css";
import { useModuleData } from "./ModuleWrapper";

const tsToIsoFormat = (ts: number): string => {
  const date = new Date(ts);
  return date.toISOString();
};

const isotimeToTs = (isotime: string): number => new Date(isotime).getTime();

const findCurrentEvent = (events: IEvent[], time: number) => {
  for (let index = 0; index < events.length; index++) {
    const event = events[index];

    const start = isotimeToTs(event.time);
    const end = isotimeToTs(event.endTime || "");

    // console.log("ASDBG start", event.time, start);
    // console.log("ASDBG end  ", event.endTime, end);
    // console.log("ASDBG time ", time);
    // console.log(
    //   "ASDBG delta ",
    //   time - start,
    //   end - start,
    //   time >= start && time <= end
    // );
    if (time >= start && time <= end) {
      return event;
    }
  }

  return null;
};

const datapointFromData = (
  data: ModuleData,
  streamName: string,
  currentEvent: IEvent | null
) => {
  const stream = data.streams[streamName];
  if (
    !stream ||
    !stream.data ||
    !stream.data.length ||
    !stream.data[0].points.length
  ) {
    return null;
  }

  const datapoints = stream.data[0].points;

  if (!currentEvent || !currentEvent.endTime) {
    return datapoints[datapoints.length - 1];
  }

  const startTs = isotimeToTs(currentEvent.time);
  const endTs = isotimeToTs(currentEvent.endTime);

  for (let index = 0; index < datapoints.length; index++) {
    const [ts, value] = datapoints[index];

    if (startTs <= ts && ts <= endTs) {
      console.log("ASDBG selected datapoint", streamName, [ts, value]);
      return value;
    }
  }

  return null;
};

const eventNameToColor = (eventId: string | undefined) => {
  if (!eventId) return "none";

  const parsedId = +eventId;
  if (!parsedId) return "none";

  if (parsedId % 3 === 0) return "#900";
  else if (parsedId % 3 === 1) return "#090";
  return "#009  ";
};

function Module() {
  const moduleData = useModuleData();

  const [device, setDevice] = React.useState<Device | undefined>();

  const [coords, setCoords] = React.useState({ x: 0, y: 0 });
  const [baseImgUrl, setBaseImgUrl] = React.useState("");
  const [overlayImgUrl, setOverlayImgUrl] = React.useState("");
  const [currentEvent, setCurrentEvent] = React.useState<IEvent | null>(null);

  React.useEffect(() => {
    Authentication.waitTilAuthenticated()
      .then(() => {
        console.log("ASDBG authed");
        return Fleet.getCurrentDevice();
      })
      .then((device) => {
        console.log("ASDBG got device", device);
        setDevice(device);
      });
  }, []);

  React.useEffect(() => {
    const fetchEvent = async () => {
      if (!moduleData || !device) return;

      if (
        currentEvent &&
        isotimeToTs(currentEvent.time) <= moduleData.time &&
        moduleData.time <= isotimeToTs(currentEvent.endTime || "")
      ) {
        // no need to update if inside current event
        return;
      }

      const events = await Fleet.queryEvents({
        deviceIds: [device.id],
        eventTypes: ["annotation"],
        start: tsToIsoFormat(moduleData.queryRange.start - 60_000),
        end: tsToIsoFormat(moduleData.queryRange.end + 60_000),
      });
      // console.log("ASDBG events", events);

      const newCurrentEvent = findCurrentEvent(events, moduleData.time);
      console.log("Current event", newCurrentEvent);
      setCurrentEvent(newCurrentEvent);
    };

    fetchEvent();
  }, [moduleData, device, currentEvent]);

  React.useEffect(() => {
    if (!moduleData) return;

    const baseImgUrl = datapointFromData(
      moduleData,
      "prediction_base",
      currentEvent
    )?.url;
    if (baseImgUrl) {
      setBaseImgUrl(baseImgUrl);
    }
    const overlayImgUrl = datapointFromData(
      moduleData,
      "prediction_overlay",
      currentEvent
    )?.url;
    if (overlayImgUrl) {
      setOverlayImgUrl(overlayImgUrl);
    }
    const pickPosUrl = datapointFromData(
      moduleData,
      "predictions_coords",
      currentEvent
    );
    if (pickPosUrl) {
      fetch(pickPosUrl)
        .then((res) => res.json())
        .then((data) => setCoords(data));
    }
  }, [moduleData, currentEvent]);

  if (!device) {
    return <>Loading...</>;
  }

  return (
    <div className="App">
      <div className="container-thumb">
        <object>
          <img
            src={baseImgUrl}
            alt=""
            loading="lazy"
            className="image-thumb"
            title=""
          />
        </object>
        <div className="overlay-thumb">
          <img
            src={overlayImgUrl}
            alt=""
            loading="lazy"
            className="image-thumb"
          />
        </div>
        <svg className="top-left" viewBox="0 0 1280 720">
          <circle
            cx={coords.x}
            cy={coords.y}
            r="10"
            stroke="orange"
            strokeWidth="3"
            fill="none"
          ></circle>
        </svg>
      </div>
      {currentEvent ? (
        <div
          style={{
            backgroundColor: eventNameToColor(currentEvent.tags?.item_id),
          }}
        >
          <h1 style={{ padding: "0 20px" }}>
            Current attempt: {currentEvent.message}
          </h1>
        </div>
      ) : (
        <div>
          <h1 style={{ padding: "0 20px" }}>No active attempt found</h1>
        </div>
      )}
    </div>
  );
}

export default Module;
