import React from "react";
import { ModuleData, App } from "@formant/data-sdk";

const context = React.createContext<ModuleData | null>(null);
const useModuleData = () => React.useContext(context);

const ModuleDataProvider = ({ children }: { children: React.ReactNode }) => {
  const [data, setData] = React.useState<ModuleData | null>(null);

  React.useEffect(() => {
    App.setModuleDateTimeRange(30_000, 30_000);
    App.addModuleDataListener((newData) => setData(newData));
  }, []);

  return <context.Provider value={data}>{children}</context.Provider>;
};

export { ModuleDataProvider, useModuleData };
